<template>
  <div v-transfer-dom :data-transfer="transfer">
    <transition name="fade">
      <div class="yanxia-dragbox" ref="dragBox" :style="mainStyles" v-show="visible">
        <div class="yanxia-dragbar" ref="dragBar">
          <h3 class="yanxia-dragbar-title">{{title}}</h3>
          <span class="yanxia-close" @click="dragClose">
            <i class="yanxia-close-icon"></i>
          </span>
        </div>
        <slot></slot>
      </div>
    </transition>
  </div>
</template>

<script>
  import yanxiaDrag from "./util/yanxiaDrag";
  import TransferDom from "./util/transfer-dom";
  export default {
    name: "yanxia-drag",
    directives: {
      TransferDom
    },
    props: {
      value: {
        type: Boolean,
        default: false
      },
      transfer: {
        type: Boolean,
        default: true
      },
      dragBar: {
        type: String,
        default: ""
      },
      dragBox: {
        type: String,
        default: ""
      },
      title: {
        type: String,
        default: ""
      },
      left: {
        type: String,
        default: ""
      },
      top: {
        type: String,
        default: ""
      }
    },
    data() {
      return {
        visible: this.value
      };
    },
    computed: {
      mainStyles() {
        let style = {};
        const left = parseInt(this.left);
        const styleLeft = {
          left: `${left}px`
        };
        const top = parseInt(this.top);
        const styleTop = {
          top: `${top}px`
        };
        Object.assign(style, styleLeft, styleTop);
        return style;
      }
    },
    mounted() {
      if (this.visible) {
        this.initYanXiaDrag();
      }
    },
    methods: {
      initYanXiaDrag() {
        yanxiaDrag(this.$refs.dragBar, this.$refs.dragBox);
      },
      dragClose() {
        this.visible = false;
        this.$emit("input", false);
        this.$emit("on-close");
      }
    },
    watch: {
      value(val) {
        this.visible = val;
      },
      visible(val) {
        this.initYanXiaDrag();
        this.$emit("value", false);
      }
    }
  };

</script>

<style>
  .yanxia-dragbox {
    width: 360px;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
    transition: opacity 0.5s;
    background-color: #fff;
    left: calc(50vw - (360px/2));
    top: 15vh;
    z-index: 99999999;
  }

  .yanxia-dragbar {
    width: 100%;
    height: 56px;
    background-color: rgb(116, 116, 116);
    cursor: move;
    padding: 0 8px;
    position: relative;
    box-sizing: border-box;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.258824),
    0 2px 10px 0 rgba(0, 0, 0, 0.156863);
  }

  .yanxia-dragbar .yanxia-dragbar-title {
    padding: 0 0 0 6px;
    margin: 0;
    line-height: 56px;
    font-size: 21px;
    font-weight: 400;
    color: #fff;
  }

  .yanxia-close {
    display: block;
    width: 24px;
    height: 24px;
    position: absolute;
    top: 16px;
    right: 12px;
    cursor: pointer;
  }

  .yanxia-close-icon {
    width: 24px;
    height: 24px;
    position: relative;
    display: block;
    overflow: hidden;
  }

  .yanxia-close-icon::before,
  .yanxia-close-icon::after {
    content: "";
    position: absolute;
    height: 3px;
    width: 100%;
    top: 50%;
    left: 0;
    margin-top: -1px;
    background: #fff;
    margin-top: -4px;
  }

  .yanxia-close-icon::before {
    transform: rotate(45deg);
  }

  .yanxia-close-icon::after {
    transform: rotate(-45deg);
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: opacity 0.3s;
  }

  .fade-enter,
  .fade-leave-to {
    opacity: 0;
  }

</style>



