var yanxiaDrag = function (dragBar, target) {

  var dragParams = {
    left: 0,
    top: 0,
    currentX: 0,
    currentY: 0,
    distanceX: 0,
    distanceY: 0,
    flag: false,
  }
  var getCss = function (o, key) {
    return o.currentStyle ? o.currentStyle[key] : document.defaultView.getComputedStyle(o, false)[key];
  };

  if (getCss(target, "position") !== "absolute") {
    target.style.position = "absolute";
  }
  if (getCss(target, "left") !== "auto") {
    dragParams.left = getCss(target, "left");
  }
  if (getCss(target, "top") !== "auto") {
    dragParams.top = getCss(target, "top");
  }

  dragBar.onmousedown = function (event) {
    dragParams.flag = true;

    if (!event) {
      event = window.event;

      dragBar.onselectstart = function () {
        return false;
      }
    }
    var e = event;

    dragParams.currentX = e.clientX;
    dragParams.currentY = e.clientY;
  };

  document.onmouseup = function () {
    dragParams.flag = false;
    if (getCss(target, "left") !== "auto") {
      dragParams.left = getCss(target, "left");
    }
    if (getCss(target, "top") !== "auto") {
      dragParams.top = getCss(target, "top");
    }
  };
  document.onmousemove = function (event) {
    var e = event ? event : window.event;
    if (dragParams.flag) {

      var nowX = e.clientX,
        nowY = e.clientY;

      dragParams.distanceX = nowX - dragParams.currentX,
        dragParams.distanceY = nowY - dragParams.currentY;

      var nowLeft = parseInt(dragParams.left) + dragParams.distanceX;
      var nowTop = parseInt(dragParams.top) + dragParams.distanceY

      var xMax = document.documentElement.clientWidth - target.offsetWidth,
        yMax = document.documentElement.clientHeight - target.offsetHeight;

      if (nowLeft <= 0) {
        nowLeft = 0
      } else if (nowLeft >= xMax) {
        nowLeft = xMax
      }

      if (nowTop <= 0) {
        nowTop = 0
      } else if (nowTop >= yMax) {
        nowTop = yMax
      }

      target.style.left = nowLeft + "px"
      target.style.top = nowTop + "px"

      if (event.preventDefault) {
        event.preventDefault();
      }
      return false;
    }
  }
}

export default yanxiaDrag



